'use client'

import { useEffect, useState } from 'react';

const dayOfWeekMap = ['Ch·ªß nh·∫≠t', 'Th·ª© 2', 'Th·ª© 3', 'Th·ª© 4', 'Th·ª© 5', 'Th·ª© 6', 'Th·ª© 7'];

export default function CurrentTime() {
  const [now, setNow] = useState<Date | null>(null);

  useEffect(() => {
    const updateTime = () => setNow(new Date());

    updateTime(); // l·∫ßn ƒë·∫ßu
    const interval = setInterval(updateTime, 1000);

    return () => clearInterval(interval);
  }, []);

  if (!now) return null; // tr√°nh mismatch khi ch∆∞a c√≥ date

  const formatTime = (date: Date) => {
    const day = dayOfWeekMap[date.getDay()];
    const dd = date.getDate().toString().padStart(2, '0');
    const mm = (date.getMonth() + 1).toString().padStart(2, '0');
    const yyyy = date.getFullYear();
    const hh = date.getHours().toString().padStart(2, '0');
    const min = date.getMinutes().toString().padStart(2, '0');
    const ss = date.getSeconds().toString().padStart(2, '0');

    return `${day}, ${dd}/${mm}/${yyyy} ${hh} : ${min} : ${ss}`;
  };

  return (
    <div className="text-sm text-gray-600">
      {formatTime(now)}
    </div>
  );
}
